@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Roboto:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Roboto:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Roboto:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Castoro:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Martel+Sans:wght@200;300;400;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Saira+Stencil+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.masthead{display:flex;justify-content:space-between;height:65px;align-items:center}.masthead__title{font-family:"DM Serif Display";font-size:30px;margin-left:125px;text-decoration:none;color:black}.masthead__login{margin-right:125px;font-weight:300}.masthead__login .button{color:black;text-decoration:none;margin:0 10px;font-weight:600}.masthead__link{color:white;background-color:green;text-decoration:none;padding:5px 10px;border-radius:5px;margin-left:15px}

.button{font-family:Helvetica, sans-serif;border-radius:4px;font-size:16px;padding:10px 20px;background:rgba(0,0,0,0)}.button__transparent{color:black;border:1px solid black}.button__user{color:white;background-color:black;justify-self:center}.button__delete{color:white;background-color:red;border:none}

.landing-page{font-family:"DM Serif Display";display:flex;flex-direction:column;align-items:center;text-align:center}.landing-page__content{margin-top:100px}.landing-page .header{font-size:70px;width:800px}.landing-page .sub-header{font-size:30px;margin-top:20px}.landing-page .button{margin:20px}

.post-preview{font-family:Times, "Times New Roman", serif;font-style:unset;text-decoration:none;font-family:"Roboto";width:50%;display:flex;color:black;justify-content:space-between;align-items:center}.post-preview h1{font-size:16px;margin:4px 0;font-weight:500}.post-preview h3{font-size:16px;margin:4px 0;font-weight:300;color:#757575}.post-preview__separator{margin:0px 5px}.post-preview__meta-data{font-weight:300;font-size:13px;margin:5px 0}.post-preview__avatar{height:25px;width:25px;margin-right:5px}.post-preview__author{font-size:13px;font-weight:500;display:flex;align-items:center}.post-preview .thumbnail{width:150px;height:115px}

.post-index-page{margin-left:125px;margin-top:50px}.post-index-page .preview{margin-bottom:25px}

.user-form{display:flex;justify-content:center;font-family:"Castoro";font-size:24px}.user-form .error{font-size:14px;font-family:"Roboto";color:white;padding:5px;border-radius:5px;text-decoration:none;background-color:rgba(255,0,0,0.8);opacity:0}.user-form .visible{opacity:1}.user-form div{margin:10px 0;text-decoration:underline}.user-form label{margin:10px 0;display:flex;flex-direction:column;font-family:"Martel Sans";font-size:16px}.user-form form{display:flex;flex-direction:column;padding:20px;box-shadow:4px 2px 4px 3px #dfdfe2;border-radius:5px;width:20%;margin-top:10%}.user-form input{margin:10px 0;width:60%;border-top:0px;border-right:0px;border-left:0px}.user-form input:focus{outline:none}

.create-post-form{margin:3% 10%;font-family:"Castoro";font-size:24px}.create-post-form .title{margin-bottom:20px}.create-post-form__border{border:1px solid black;padding:10px;border-radius:3px;height:300px;overflow:scroll}.create-post-form label{font-size:18px}.create-post-form__inputs{display:flex;flex-direction:column}.create-post-form__inputs label{margin:10px 0;display:flex;flex-direction:column}.create-post-form__inputs input{width:400px;height:25px}.create-post-form button{margin-top:30px}.create-post-form .buttons{display:flex;justify-content:space-between}

.by-line{display:flex;font-family:"Roboto";margin:30px 0}.by-line__avatar{height:40px;width:40px;margin-right:10px}.by-line__separator{margin:0px 5px}.by-line__data{display:flex;flex-direction:column;font-weight:300;font-size:14px;justify-content:space-around}.by-line__data-secondline{display:flex;color:#757575}

.post{font-style:unset;color:black;font-family:"Castoro";font-weight:500;margin:0 20%;padding-top:42px;margin-bottom:30px}.post h1{font-size:40px;font-weight:500;margin:10px 0px}.post h3{margin:10px 0;font-size:20px;font-weight:300;color:#757575;font-family:"Roboto"}.post p{font-size:21px;color:#292929;margin:42px 0}.post__image{width:100%}.post .engagment{display:flex}.post .claps,.post .comments{display:flex;margin-bottom:30px;margin-right:20px;align-items:center}.post .claps img,.post .comments img{height:30px;width:30px}.post .claps span,.post .comments span{font-family:"Roboto";margin-left:5px;font-weight:300;font-style:italic}

.create-post-page{margin:30px}.create-post-page .preview{border:1px dotted grey;border-radius:5px;font-family:"Saira Stencil One";position:relative}.create-post-page .preview .prev-text{color:red;position:absolute;font-size:30px;opacity:0.1;transform:rotate(-20deg);left:10%;top:50px;z-index:10}

.comment{font-family:"Roboto";margin-left:30px;margin-top:20px;font-weight:300}.comment .text{font-size:14px;padding:25px 0px}.comment .avatar{height:50px;width:50px;margin-right:10px}.comment .username{font-size:14px;display:flex;align-items:center}.comment .username div{font-size:14px}.comment .username div div{color:#757575}.comment input{margin:0 10px}

.comment-section{position:fixed;left:100%;box-shadow:0px 4px 12px rgba(0,0,0,0.15);background-color:white;width:400px;height:100vh;display:flex;flex-direction:column;overflow-y:scroll;border-bottom:1px dotted gray;transition:0.5s}.comment-section .header{font-family:"Roboto";font-size:20px;padding:24px;font-weight:500;display:flex;justify-content:space-between;align-items:center}.comment-section .close{height:25px;width:25px;align-self:flex-end;margin-right:30px}.comment-section .comments .comment{border-bottom:1px dotted gray}.comment-section .comments .comment:last-child{border-bottom:none}.comment-section input{width:80%;align-self:center;padding:15px;border-radius:3px;border:none;box-shadow:rgba(0,0,0,0.12) 0px 2px 8px;margin-bottom:10px}.comment-section.enter{transform:translateX(-100%);transition:0.5s}

/* Document
